@import url(https://fonts.googleapis.com/css2?family=Domine:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.navSection{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}
*{font-family:"Times New Roman",Times,serif}article{width:300px;height:auto;margin:15px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}article img{-webkit-justify-content:center;justify-content:center;width:300px;height:150px}article span{margin:20px}h6{margin:0;margin:6px,0,6px,0;font-size:18px}a{color:#000;text-decoration:none}a:hover{text-shadow:.5px .5px #b3b3b3}.else-color{background-color:#cd5c5c}.date-src{display:-webkit-flex;display:flex}.journalContent{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:5px}.img-cont{width:100%;text-align:center}.ole{background-color:#94be1f}.redJournals{background-color:#d70026}.blueJournals{background-color:#0074c4}.infobae{background-color:#f68e1e}.cronista{background-color:#3d939b}.abc{background-color:#000;color:#fff}
.topJournals,.title{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}h1{margin:30px}h4{padding:20px 20px 0px 20px}.googleBar{border-bottom:6px solid #0074c4}.abcBar{border-bottom:6px solid #94be1f}.infobaeBar{border-bottom:6px solid #f68e1e}.nacionBar{border-bottom:6px solid #d70026}
input{margin:55px 0 20px 5px;width:350px}.search-content{margin-left:20px}.search-icon{position:relative;top:8px}
